/**
 * 
 * @param {string|number} id - ID
 * @param {Object} data - 
 * @returns {Promise}
 */
export function addContractAttachment(id, data) {
  return request({
    url: `/api/contract/contracts/${id}/add_attachment/`,
    method: 'post',
    data,
    headers: {
      'Content-Type': 'multipart/form-data'
    }
  })
}

/**
 * (AI)
 * @param {string|number} id - ID
 * @param {Object} data - content
 * @returns {Promise}
 */
export function checkContract(id, data) {
  // ID
  if (!id || isNaN(Number(id))) {
    console.error('ID:', id);
    return Promise.reject(new Error('ID'));
  }
  
  // content
  if (!data || !data.content) {
    console.error(':', data);
    return Promise.reject(new Error(''));
  }
  
  // 
  const contentLength = data && data.content ? data.content.length : 0;
  console.log(`checkContract API - ID: ${id}, : ${contentLength}`);
  
  return request({
    url: `/api/contract/contracts/${id}/check_contract/`,
    method: 'post',
    data,
    timeout: 180000 // 3
  }).catch(error => {
    console.error('API:', error);
    throw error;
  });
}

/**
 * AI
 * @param {string|number} id - ID
 * @param {Object} data - content
 * @param {AbortSignal} [signal] - AbortSignal
 * @returns {Promise}
 */
export function aiPolishContract(id, data, signal) {
  // content
  if (!data || !data.content) {
    console.error(':', data);
    return Promise.reject(new Error(''));
  }
  
  const contentLength = data && data.content ? data.content.length : 0;
  console.log(`aiPolishContract API - ID: ${id}, : ${contentLength}`);
  
  return request({
    url: `/api/contract/contracts/${id}/ai_polish/`,
    method: 'post',
    data,
    signal
  }).then(response => {
    console.log('AI:', response);
    return response;
  }).catch(error => {
    console.error('AIAPI:', error);
    throw error;
  });
}

