  background: white;
  box-shadow: 0 2px 12px rgba(0, 0, 0, 0.15);
  border-radius: 8px;
  padding: 0;
  max-width: 450px;
  border: 1px solid #ebeef5;
  font-size: 14px;
  position: absolute !important;
  pointer-events: auto !important;
}

.suggestion-tooltip-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #f5f7fa;
  padding: 10px 15px;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  border-bottom: 1px solid #e4e7ed;
}

.suggestion-tooltip-title {
  font-weight: bold;
  color: #303133;
}

.suggestion-tooltip-close {
  cursor: pointer;
  color: #909399;
  font-size: 18px;
}

.suggestion-tooltip-body {
  padding: 15px;
}

.suggestion-comparison {
  display: flex;
  margin-bottom: 10px;
  gap: 10px;
}

.suggestion-column {
  flex: 1;
}

.suggestion-divider {
  width: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.suggestion-divider:before {
  content: "";
  color: #909399;
  font-size: 18px;
}

.suggestion-label {
  font-weight: bold;
  margin-bottom: 5px;
  color: #606266;
}

.suggestion-original {
  background-color: #fff5f5;
  color: #f56c6c;
  border: 1px solid #fde2e2;
  padding: 8px 12px;
  border-radius: 4px;
  word-break: break-word;
}

.suggestion-suggested {
  background-color: #f0f9eb;
  color: #67c23a;
  border: 1px solid #e1f3d8;
  padding: 8px 12px;
  border-radius: 4px;
  word-break: break-word;
}

.suggestion-diff-container {
  margin-bottom: 10px;
}

.suggestion-diff {
  background-color: #fafafa;
  border: 1px solid #ebeef5;
  padding: 8px 12px;
  border-radius: 4px;
  word-break: break-word;
}

.suggestion-explanation-container {
  margin-bottom: 10px;
}

.suggestion-explanation {
  font-style: italic;
  color: #606266;
}

.suggestion-actions {
  padding: 10px 15px;
  border-top: 1px solid #e4e7ed;
  text-align: right;
  display: flex;
  justify-content: flex-end;
  gap: 10px;
}

.suggestion-actions button {
  padding: 6px 12px;
  border-radius: 4px;
  font-size: 14px;
  cursor: pointer;
  border: 1px solid transparent;
  transition: all 0.3s;
}

.suggestion-actions .btn-adopt {
  background-color: #67c23a;
  color: white;
}

.suggestion-actions .btn-adopt:hover {
  background-color: #5daf34;
}

.suggestion-actions .btn-ignore {
  background-color: #f4f4f5;
  color: #909399;
  border-color: #dcdfe6;
}

.suggestion-actions .btn-ignore:hover {
  background-color: #e9e9eb;
}

/*  */
.suggestion-highlight, 
[data-suggestion-id],
.ql-editor [data-suggestion-id],
.ql-editor .suggestion-highlight {
  pointer-events: auto !important;
  cursor: pointer !important;
  background-color: rgba(255, 217, 102, 0.3) !important;
  border-bottom: 1px dashed #ffb300 !important;
  z-index: 999 !important;
  position: relative !important;
  display: inline-block !important;
}

/*  */
.top-navigation-bar {
  background-color: #ffffff;
  padding: 10px 20px 0;
  border-radius: 4px;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
  margin-bottom: 20px;
}

/*  */
.contract-detail-tabs .el-tabs__header {
  margin-bottom: 0;
}

.contract-detail-tabs .el-tabs__nav {
  border: none;
}

.contract-detail-tabs .el-tabs__item {
  height: 40px;
  line-height: 40px;
  font-size: 15px;
  font-weight: 500;
}

.contract-detail-tabs .el-tabs__item.is-active {
  font-weight: bold;
  color: #409eff;
}

/*  */
.card {
  border: none;
  border-radius: 6px;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
}
